import React, { useState } from 'react';
import axios from 'axios';
import { Bar, Line, Pie } from 'react-chartjs-2';
import Chart from 'chart.js/auto';
import { WiDaySunny, WiDayRain, WiDayCloudy, WiDayThunderstorm, WiDaySnow, WiCloudy, WiRain, WiThunderstorm, WiSnow, WiHumidity, WiBarometer, WiDayFog } from 'weather-icons-react';
import './App.css';

const App = () => {
  const [city, setCity] = useState('');
  const [weatherData, setWeatherData] = useState(null);
  const [forecastData, setForecastData] = useState(null);
  const [error, setError] = useState(null);
  const [unit, setUnit] = useState('metric'); // 'metric' for Celsius, 'imperial' for Fahrenheit
  const [lat, setLat] = useState(null);
  const [lon, setLon] = useState(null);
  const API_KEY = process.env.REACT_APP_API_KEY;

  const fetchWeather = async (lat = null, lon = null) => {
    const query = lat && lon
      ? `lat=${lat}&lon=${lon}`
      : `q=${city}`;
  
    try {
      const weatherResponse = await axios.get(
        `https://api.openweathermap.org/data/2.5/weather?${query}&appid=${API_KEY}&units=${unit}`
      );
      setWeatherData(weatherResponse.data);
  
      const forecastResponse = await axios.get(
        `https://api.openweathermap.org/data/2.5/forecast?${query}&appid=${API_KEY}&units=${unit}`
      );
      setForecastData(forecastResponse.data);
      
      setError(null);
    } catch (err) {
      setError('Error fetching weather data. Please try again.');
      setWeatherData(null);
      setForecastData(null);
    }
  };
  

  const handleInputChange = (e) => {
    setCity(e.target.value);
  };

  const handleSearch = (e) => {
    e.preventDefault();
    fetchWeather();
  };

  const handleGeolocation = () => {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          const { latitude, longitude } = position.coords;
          setLat(latitude);
          setLon(longitude);
          fetchWeather(latitude, longitude);
        },
        (error) => {
          setError('Error getting location. Please try again.');
        }
      );
    } else {
      setError('Geolocation is not supported by this browser.');
    }
  };

  const handleUnitToggle = () => {
    setUnit(unit === 'metric' ? 'imperial' : 'metric');
    fetchWeather(lat, lon);
  };

  const getHourlyData = () => {
    if (!forecastData) return [];
    return forecastData.list.slice(0, 8).map(item => ({
      time: item.dt_txt,
      temp: item.main.temp,
      icon: item.weather[0].icon
    }));
  };

  const get5DayData = () => {
    if (!forecastData) return [];
    return forecastData.list.filter(item => item.dt_txt.includes('15:00:00')).map(item => ({
      date: item.dt_txt.split(' ')[0],
      temp: item.main.temp,
      humidity: item.main.humidity,
      pressure: item.main.pressure
    }));
  };

  const hourlyData = getHourlyData();
  const fiveDayData = get5DayData();

  const hourlyChartData = {
    labels: hourlyData.map(item => item.time),
    datasets: [{
      label: 'Temperature (°C)',
      data: hourlyData.map(item => item.temp),
      borderColor: 'rgba(75,192,192,1)',
      borderWidth: 1,
      fill: false
    }]
  };

  const fiveDayChartData = {
    labels: fiveDayData.map(item => item.date),
    datasets: [{
      label: 'Temperature (°C)',
      data: fiveDayData.map(item => item.temp),
      borderColor: 'rgba(153,102,255,1)',
      borderWidth: 1,
      fill: false
    }]
  };

  const humidityData = {
    labels: fiveDayData.map(item => item.date),
    datasets: [{
      label: 'Humidity (%)',
      data: fiveDayData.map(item => item.humidity),
      backgroundColor: 'rgba(75,192,192,0.2)',
      borderColor: 'rgba(75,192,192,1)',
      borderWidth: 1
    }]
  };

  const pressureData = {
    labels: fiveDayData.map(item => item.date),
    datasets: [{
      label: 'Pressure (hPa)',
      data: fiveDayData.map(item => item.pressure),
      backgroundColor: 'rgba(153,102,255,0.2)',
      borderColor: 'rgba(153,102,255,1)',
      borderWidth: 1
    }]
  };

  const getWeatherIcon = (icon) => {
    switch (icon) {
      case '01d': return <WiDaySunny size={30} />;
      case '02d': return <WiDayCloudy size={30} />;
      case '09d': return <WiRain size={30} />;
      case '11d': return <WiThunderstorm size={30} />;
      case '13d': return <WiSnow size={30} />;
      default: return <WiCloudy size={30} />;
    }
  };

  return (
    <div style={{ textAlign: 'center', padding: '50px', backgroundColor: '#f0f0f0' }}>
      <h1>Weather App</h1>
      <form onSubmit={handleSearch} style={{ marginBottom: '20px' }}>
        <input
          type="text"
          placeholder="Enter city"
          value={city}
          onChange={handleInputChange}
          style={{ padding: '10px', fontSize: '16px' }}
        />
        <button type="submit" style={{ marginLeft: '10px', padding: '10px 20px' }}>
          Search
        </button>
        <button
          type="button"
          onClick={handleGeolocation}
          style={{ marginLeft: '10px', padding: '10px 20px' }}
        >
          Use My Location
        </button>
        <button
          type="button"
          onClick={handleUnitToggle}
          style={{ marginLeft: '10px', padding: '10px 20px' }}
        >
          {unit === 'metric' ? 'Switch to °F' : 'Switch to °C'}
        </button>
      </form>

      {error && <p style={{ color: 'red' }}>{error}</p>}

      {weatherData && (
        <div style={{ marginTop: '20px', background: '#fff', padding: '20px', borderRadius: '8px', boxShadow: '0 0 10px rgba(0,0,0,0.1)' }}>
          <h2>{weatherData.name}</h2>
          <p>Temperature: {weatherData.main.temp}°{unit === 'metric' ? 'C' : 'F'}</p>
          <p>Weather: {weatherData.weather[0].description}</p>
          {getWeatherIcon(weatherData.weather[0].icon)}
          <p>Humidity: {weatherData.main.humidity}%</p>
          <p>Wind Speed: {weatherData.wind.speed} m/s</p>
        </div>
      )}

      {forecastData && (
        <div style={{ marginTop: '20px' }}>
          <h2>5-Day Forecast</h2>
          <div style={{ marginBottom: '20px' }}>
            <Line data={fiveDayChartData} options={{ responsive: true }} />
          </div>
          <div style={{ marginBottom: '20px' }}>
            <Bar data={humidityData} options={{ responsive: true }} />
          </div>
          <div>
            <Bar data={pressureData} options={{ responsive: true }} />
          </div>
        </div>
      )}

      {hourlyData.length > 0 && (
        <div>
          <h2>Hourly Forecast</h2>
          <ul style={{ listStyleType: 'none', padding: 0 }}>
            {hourlyData.map((data, index) => (
              <li key={index} style={{ margin: '10px 0', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                {getWeatherIcon(data.icon)} {data.time}: {data.temp}°{unit === 'metric' ? 'C' : 'F'}
              </li>
            ))}
          </ul>
        </div>
      )}
    </div>
  );
};

export default App;